<template>
  <div class="my">
    <div v-if="isLogin">
      <div class="bg-box" :style="{backgroundImage: 'url('+userData.userBg+')'}">
        <div class="change-img">更换背景</div>
        <div class="updata-img">
          <van-uploader :max-size="maxSize" :after-read="afterRead" @oversize="overSize" />
        </div>
        <div class="up clearFix">
          <div class="img-box fl">
            <img class="auto-img" :src="userData.userImg" alt />
          </div>
          <div class="name fl">{{userData.nickName}}</div>
        </div>
        <div class="down">{{userData.desc == ""? "此用户很懒,啥也没留下" : userData.desc}}</div>
      </div>
      <div class="v-cell">
        <van-cell
          class="v-cell-box"
          :title="item.text"
          is-link
          v-for="(item,index) in textData"
          :key="index"
          @click="togglePage(index)"
        />
      </div>
    </div>
    <div v-else>
      <div class="tip">
        <van-empty class="v-tip" description="未登录啥都没有哦" />
        <span @click="goLogin">点我登录</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      textData: [
        {
          text: "账号管理",
          name: "IdMsg"
        },
        {
          text: "地址管理",
          name: "AddressList"
        },
        {
          text: "我的收藏",
          name: "MyLike"
        },
        {
          text: "安全中心",
          name: "Security"
        }
      ],
      isLogin: false,
      userData: {},
      // 限制上传图片大小
      maxSize: 1024 * 1024
    };
  },
  created() {
    this.getUserData();
    if (localStorage.getItem("__Tk")) {
      this.isLogin = true;
    }
  },
  methods: {
    // 上传背景图片
    afterRead(file) {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      // 此时可以自行将文件上传至服务器
      // 
      // 获取base64格式图片
      let imgData = file.content.split(",")[1];
      let type = file.file.type.split("/")[1];
      // 
      // 
      let tkStr = localStorage.getItem("__Tk");
      this.axios({
        method: "post",
        url: "/updateUserBg",
        data: {
          appkey: this.appkey,
          tokenString: tkStr,
          imgType: type,
          serverBase64Img: imgData
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 'I001') {
            this.userData.userBg = result.data.userBg;
          } else {
            this.$toast(result.data.msg);
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },
    // 跳转到对应页面
    togglePage(index) {
      this.$router.push({ name: this.textData[index].name });
    },
    // 跳转到登录页面
    goLogin() {
      this.$router.push({ name: "Login" });
    },
    // 获取用户数据
    getUserData() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      let tkStr = localStorage.getItem("__Tk");
      this.axios({
        method: "get",
        url: "/findMy",
        params: {
          appkey: this.appkey,
          tokenString: tkStr
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == "A001") {
            this.userData = result.data.result[0];
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },
    // 上传图片超出大小提示
    overSize() {
      this.$toast("图片大小不能超过1M哦");
    }
  }
};
</script>

<style lang="less" scoped>
.my {
  position: fixed;
  width: 100%;
  height: 100%;
  font-size: 14px;
  .bg-box {
    background-size: cover;
    background-position: center center;
    padding: 10px;
    box-sizing: border-box;
    height: 250px;
    position: relative;
    background-color: #fff;
    border-bottom-right-radius: 10px;
    border-bottom-left-radius: 10px;
    overflow: hidden;
    margin-bottom: 15px;
    .change-img {
      position: absolute;
      top: 10px;
      right: 10px;
      color: #ccc;
      font-size: 14px;
      border-radius: 20px;
      padding: 5px;
      box-shadow: 2px 2px 2px slateblue;
      background-color: rgba(0, 0, 0, 0.5);
    }
    .updata-img {
      position: absolute;
      top: 10px;
      right: 10px;
      height: 20px;
      width: 50px;
      overflow: hidden;
      opacity: 0;
    }
    .up {
      width: 100%;
      height: 70px;
      padding-top: 90px;
      .img-box {
        width: 70px;
        height: 70px;
        border-radius: 10px;
        box-shadow: 1px 1px 3px 1px #ddd;
        overflow: hidden;
        margin-right: 20px;
        img{
          height: 100%;
        }
      }
      .name {
        line-height: 70px;
        color: slateblue;
        font-weight: 600;
        font-size: 16px;
      }
    }
    .down {
      margin-left: 90px;
      color: #fff;
      background-color: rgba(0, 0, 0, 0.2);
      padding: 5px;
      border-radius: 10px;
    }
  }
  .v-cell {
    padding: 10px;
    background-color: #fff;
    border-radius: 10px;
    .v-cell-box {
      margin: 10px 0;
      box-shadow: 1px 1px 2px 1px #ddd;
      border-radius: 20px;
      color: slateblue;
    }
  }
  .tip {
    width: 100%;
    text-align: center;
    height: 150px;
    // line-height: 150px;
    position: relative;
    span {
      // border: 1px solid #ccc;
      top: 50%;
      height: 30px;
      padding: 10px;
      border-radius: 20px;
      color: slateblue;
      background-color: #fff;
      box-shadow: 1px 1px 2px 1px #ddd;
    }
    .v-tip {
      width: 100%;
      height: 300px;
    }
  }
}
</style>